* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  padding: 30px;
  font-family: 'Lato', sans-serif;
  height: 100vh;
  overflow: hidden;
  background-position: center;
  position: relative; }
  body .container {
    margin-left: auto;
    margin-right: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: grid;
    grid-template-rows: 30% 40% 10% 20%;
    height: 45%;
    width: 55%;
    border: 1px solid grey;
    background: rgba(255, 255, 255, 0.8);
    border-radius: 10px; }
    body .container .row-1 {
      display: grid;
      grid-template-columns: 20% 80%; }
      body .container .row-1 .c {
        display: flex;
        align-items: center; }
        body .container .row-1 .c .quote-icon {
          padding-left: 8%;
          width: 100%;
          height: 100%; }
    body .container .row-2 {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 5%; }
    body .container .row-3 .author {
      float: right;
      margin: 10px; }
    body .container .row-4 .tweet-button {
      margin: 10px;
      float: left;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 22.5%;
      height: 40px;
      border: solid 2px #ECEDEF;
      border-radius: 10px;
      -webkit-transition: all .3s;
      transition: all .3s; }
      body .container .row-4 .tweet-button:hover {
        cursor: pointer;
        -webkit-transform: scale(1.1);
                transform: scale(1.1); }
      body .container .row-4 .tweet-button .tweet-button,
      body .container .row-4 .tweet-button .next-button {
        fill: white;
        width: 2em; }
      body .container .row-4 .tweet-button svg {
        width: 2em; }
    body .container .row-4 .next-button {
      margin: 10px;
      float: right;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 8%;
      height: 40px;
      border: solid 2px #ECEDEF;
      border-radius: 10px;
      -webkit-transition: all .3s;
      transition: all .3s; }
      body .container .row-4 .next-button:hover {
        cursor: pointer;
        -webkit-transform: scale(1.1);
                transform: scale(1.1); }
      body .container .row-4 .next-button .tweet-button,
      body .container .row-4 .next-button .next-button {
        fill: white;
        width: 10px; }
      body .container .row-4 .next-button svg {
        width: 10px; }

